import { useState, useEffect } from "react";
import { Form, Row, Button, FloatingLabel, Col, Card } from "react-bootstrap"
import { useSelector, useDispatch } from 'react-redux';
import {
    addTransaction, setToaster
} from '../hisaabSlice';


export default function AddTransaction(props) {
    const groupId = props.transaction?.groupId || 1;
    const dispatch = useDispatch();
    const [amount, setAmount] = useState(props.transaction?.amount);
    const expenseTypes = [{ type: 'Food', id: '1' }, { type: 'Travel', id: '2' }, { type: 'Entertainment', id: '3' }, { type: 'Other', id: '4' }]
    const [expType, setExpType] = useState(props.transaction?.amount || expenseTypes[0]);
    const [expDesc, setExpDesc] = useState(props.transaction?.desc);
    const checkedMembers = props.transaction?.among || [];
    const members = useSelector((state) => state.hisaab.members.filter(m => m.groupId == groupId && m.inactive != 1));
    const [owner, setOwner] = useState(props.transaction?.among || members[0]?.id);
    const createTransaction = () => {
        let transaction = { groupId: groupId, type: expType.type, datetime: Date.now(), desc: expDesc, amount: amount, among: checkedMembers.filter(m => m.isChecked == true).map(m => { return m.id }), spentBy: owner }
        dispatch(addTransaction(transaction));
        dispatch(setToaster({ show: true, text: 'Transaction Added!' }));

    }
    const colors = ['light'];

    const handleExpenseType = (e) => {
        console.log(e.target.selectedIndex);
        setExpType(expenseTypes[e.target.selectedIndex])
    }
    const handleSubmit = (e) => {
        e.preventDefault();
        if (!isNaN(owner) && checkedMembers.length > 0) {
            createTransaction();
            e.target.reset();
            setAmount();
            setExpDesc();
        }
    }
    const handleExpenseTypeInput = (e) => {
        setExpType({ type: e.target.value, id: 0 })
    }
    const handleCheckedMembers = (e) => {
        var indx = checkedMembers.findIndex(m => m.id == e.target.value);
        if (indx == -1)
            checkedMembers.push({ id: e.target.value, isChecked: e.target.checked })
        else
            checkedMembers[indx].isChecked = e.target.checked;
    }
    return (
        <Form onSubmit={handleSubmit} className='mb-5'>
            {members.length >= 0 ? <div>
                <div className="input-group">
                    <span className="input-group-text bg-light">₹</span>
                    <input type="number" className="form-control" value={amount} onChange={(e) => setAmount(e.target.value)} aria-label="Dollar amount (with dot and two decimal places)" placeholder="00.00" />
                    <span className="input-group-text bg-light">By</span>
                    <select value={owner} onChange={(e) => setOwner(e.target.value)} className="form-select" aria-label="Paid By">
                        {members.map(option => <option key={option.id} value={option.id}>{option.name}</option>)}
                    </select>
                </div>
                <br></br>
                <div className="text-center mb-3">
                    <FloatingLabel controlId="floatingSelect" label="For" size="sm">
                        <Form.Select aria-label="Floating label select example" onChange={handleExpenseType}>
                            {expenseTypes.map(m => <option key={m.id} value={m.id}>{m.type}</option>)}
                        </Form.Select>
                    </FloatingLabel>
                </div>
                <div className="input-group mb-3">
                    <input type="text" className="form-control" value={expType.type} onChange={handleExpenseTypeInput} aria-label="Text input with segmented dropdown button" />
                    <span className="input-group-text bg-light">@</span>
                    <input type="text" className="form-control" value={expDesc} onChange={(e) => setExpDesc(e.target.value)} aria-label="Text input with segmented dropdown button" />
                </div>
                <br></br>
                <legend>Split Between?</legend>
                <br></br>
                <br></br>
                <Row xs={3} md={2} className="g-4">
                    {members.map((member) => (
                        <Col className='mt-1 text-start' key={`inline-${member.id}`}>
                                    <Form.Check
                                        className="text-dark"
                                        key={`inline-${member.id}`}
                                        inline
                                        type='checkbox'
                                        id={member.id}
                                        value={member.id}
                                        label={member.name.split(' ')[0]}
                                        onChange={handleCheckedMembers}
                                        checked={checkedMembers.find(m => m.id == member.id)?.isChecked}
                                    />
                        </Col>
                    ))}
                </Row>
                <div className="mb-3">

                </div>
                <br></br>
                {<div style={{ textAlign: 'center', margin: '10' }}>
                    <button type="submit" className="btn btn-primary btn-sm" >Add Expense</button>
                </div>}
            </div> :
                <div style={{ textAlign: 'center' }}><span >Try Adding Members First !</span></div>
            }
            <br></br><br></br>
        </Form>
    )
}