import { useState, useEffect } from "react";
import { Form, Row, Button, InputGroup, FormControl, Col } from "react-bootstrap"
import { useSelector, useDispatch } from 'react-redux';
import {
    addMember,
} from '../hisaabSlice';
import EditMember from "../subcomponents/edit-member";
import Member from "../subcomponents/member";

export default function AddMember(props) {

    const groupId = props.transaction?.groupId || 1;
    const dispatch = useDispatch();
    const [name, setName] = useState('');
    const [invalid, setInValid] = useState(false);
    const [modal, setModal] = useState({ show: false, member: {} });
    const members = useSelector((state) => state.hisaab.members.filter(m => m.groupId == props.groupId || groupId && m.inactive != 1));
    const AddMember = () => {
        if (name && members.findIndex(m => m.name == name) == -1) {
            dispatch(addMember({ name: name, groupId: props.groupId || groupId }));
            setName('');
        }
    }

    function handleInput(e) {
        console.log('handling invalid')
        if (members.findIndex(m => m.name.toLowerCase() == e.target.value.toLowerCase()) == -1) {
            setInValid(false);
        }
        else {
            setInValid(true);
        }
        setName(e.target.value);
    }


    const hideModal = () => setModal({ show: false });
    const showModal = (e) => {
        setModal({ show: true, member: members.find(m=>m.id==e.currentTarget.dataset.memberid) });
    };
    return (
        <div className="mb-5">
            <Form noValidate onSubmitCapture={(e) => e.preventDefault()}>
                <InputGroup className="mb-2">
                    <Form.Control type="text" placeholder="Enter Name of Member" value={name} required onChange={handleInput} isInvalid={invalid} />
                    <Form.Control.Feedback type="invalid">
                        Please choose a different name. Member Already Exists!
                    </Form.Control.Feedback>
                    <Button variant="primary" type="submit" className="btn-sm" onClick={AddMember}>Add
                    </Button>
                </InputGroup>
            </Form>
            <div>
                <br></br>
                <Row xs={3} md={2} className="g-4">
                    {members.map(member => {
                         return member.name.toLowerCase().includes(name.toLowerCase())&&<Col  key={member.id}><Member key={member.id} member={member} handleModal={showModal} ></Member>
                        </Col>
                    })}
                </Row>
            </div>
            <EditMember data={modal} hide={hideModal}></EditMember>
        </div>
    )
}