import React from 'react';
import './App.css';
import { Hisaab } from './features/hisaab/hisaab';
import { BrowserRouter, Switch } from 'react-router-dom';
import HisaabLayoutRoute from './features/hisaab/layout';

function App() {
  return (
    <div className="App">
      <BrowserRouter basename={'/apnahisaab'}>
        <Switch>
          <HisaabLayoutRoute path="/"  component={Hisaab} />
        </Switch>
      </BrowserRouter>
    </div>
  );
}

export default App;
