import { Card, Button } from "react-bootstrap";
import {
    removeMember
} from '../hisaabSlice';
import { useDispatch } from 'react-redux';
import React from "react";
import { PencilFill } from "react-bootstrap-icons";
import style from './../hisaab.module.css';
import { getColor } from './colors';

export default React.memo(props => {
    let variant = getColor(props.member.id);
    const dispatch = useDispatch();
    return (
        <Card
            key={props.member.id}
            text='white'
            style={{ height: '100%' }}
            className={`${variant} mb-2`}
        >
            <Card.Body>
                <Card.Title as='h6'>{props.member.name.split(' ')[0]} </Card.Title>
            </Card.Body>
            <Card.Footer className="p-1">
                <PencilFill onClickCapture={props.handleModal} data-memberid={props.member.id} className={style.editButton}></PencilFill>
            </Card.Footer>
        </Card>
    )
})