import React from 'react';
import NavBar from './subcomponents/navbar';
import { Switch, Route, useRouteMatch, Redirect } from 'react-router-dom';
import addMember from './pages/add-member';
import transactions from './pages/transactions';
import addTransaction from './pages/add-transaction';
import calculations from './pages/calculations';
import { Container } from 'react-bootstrap';
import Toaster from './subcomponents/toaster';


const HisaabLayout = ({ children }) => {
  let match = useRouteMatch();
  return (
    <div>
      <div> </div>
      <NavBar></NavBar>
      <br></br>
      <Container style={{marginBottom:40}}>
        <Switch>
          <Route exact path={match.path}>
            <Redirect  to={`/members`}></Redirect>
          </Route>
          <Route path={`/members`} component={addMember}></Route>
          <Route path={`/transactions`} component={transactions}></Route>
          <Route path={`/add-transaction`} component={addTransaction}></Route>
          <Route path={`/calculations`} component={calculations}></Route>
        </Switch>
      </Container>
      <Toaster></Toaster>
    </div>
  );
}

const HisaabLayoutRoute = ({ component: Component, ...rest }) => {
  return (
    <Route {...rest} render={matchProps => (
      <HisaabLayout>
        <Component {...matchProps} />
      </HisaabLayout>
    )} />
  )
};

export default HisaabLayoutRoute;


