import { Container, Nav } from "react-bootstrap";
import { PersonPlus, ArrowLeftRight, Calculator, Funnel } from 'react-bootstrap-icons';
import { Link, useLocation, useRouteMatch } from 'react-router-dom';
import {IsMobile} from '../../helpers/screenSize'
import style from './../hisaab.module.css'
export default function NavBar() {
    const isMobile = IsMobile();
    let location = useLocation().pathname.split("/").pop();
    const match = useRouteMatch();
    match.url = match.url=='/'?'':match.url;
    console.log(match);
    return (
        <Container fluid >
            <Nav justify variant="pills" className={isMobile ? style.navbarStyle: "top"} defaultActiveKey={location||"members"}>
                <Nav.Item>
                    <Nav.Link as={Link} to={`${match.url}/members`} eventKey="members"><PersonPlus size={25}></PersonPlus></Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link as={Link} to={`${match.url}/add-transaction`} eventKey="add-transaction"><Funnel size={25}></Funnel></Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link as={Link} to={`${match.url}/transactions`} eventKey="transactions"><ArrowLeftRight size={25}></ArrowLeftRight></Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link as={Link} to={`${match.url}/calculations`} eventKey="calculations" ><Calculator size={25}></Calculator>
                    </Nav.Link>
                </Nav.Item>
            </Nav>
            
        </Container>
    )
}