import { Button } from "react-bootstrap";
import { useSelector, useDispatch } from 'react-redux';
import {
    deleteGroup
} from '../hisaabSlice';
export default function Calculations(props)
{
    const dispatch = useDispatch();
    const groupId = props.transaction?.groupId || 1;

    return(
        <div>
            calculations<br></br><br></br>
           <div><Button variant="danger" onClick={()=>dispatch(deleteGroup(groupId))} > Reset</Button></div> 
            </div>
    )
}