import { Modal, Form, Button, Row, Col, InputGroup } from "react-bootstrap";
import { useEffect, useState } from "react";
import {
    updateMember, setToaster, removeMember
} from './../hisaabSlice';
import { useDispatch } from 'react-redux';
import { getColor } from './colors';
import { Person, Envelope, Telephone, Building, TypeItalic } from "react-bootstrap-icons";
import WarningButton from "./warning-button";

export default function EditMember(props) {
    const dispatch = useDispatch();
    const [member, setMember] = useState(props.data?.member || {});
    const [name, setName] = useState(props.data.member?.name || '');
    const [org, setOrg] = useState(props.data.member?.organisation || '');
    const [email, setEmail] = useState(props.data.member?.email || '');
    const [mobile, setMobile] = useState(props.data.member?.mobile || '');
    useEffect(() => {
        setName(props.data.member?.name || '');
        setEmail(props.data.member?.email || '');
        setMobile(props.data.member?.mobile || '');
        setMember(props.data.member || {})
        setOrg(props.data.member?.organisation || '')
    }, [props.data.member]);

    const onDeleteMember = (e) => {
        console.log('trihe')
        dispatch(removeMember({ id: member.id }));
        props.hide();
    }
    const handleSubmit = (e) => {
        e.preventDefault();
        const updatedMember = { ...member, name: name, email: email, mobile: mobile, organisation: org };
        dispatch(updateMember(updatedMember))
        dispatch(setToaster({ show: true, text: 'Update Successful!' }));
    }
    let variant = getColor(member.id);

    return (
        <Modal show={props.data.show} fullscreen={true} onHide={props.hide} animation={false}>
            <Modal.Header className={variant} closeButton closeVariant='white'>
                <Modal.Title className='text-light'>{name}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form onSubmit={handleSubmit}>

                    <Row>
                        <Col><span className="fst-italic">Personal Information</span></Col>
                    </Row>
                    <br></br>
                    <Row className="mb-3">
                        <Form.Group as={Col} controlId="formGridName">
                            <Form.Label visuallyHidden>Full Name</Form.Label>
                            <InputGroup className="mb-2">
                                <InputGroup.Text className="bg-light"><Person></Person></InputGroup.Text>
                                <Form.Control type="text" placeholder="FirstName LastName" value={name} onChange={(e) => setName(e.target.value)} />
                            </InputGroup>
                        </Form.Group>
                    </Row>
                    <Row className="mb-3">
                        <Form.Group as={Col} controlId="formGridEmail">
                            <Form.Label visuallyHidden>Email</Form.Label>
                            <InputGroup className="mb-2">
                                <InputGroup.Text className="bg-light"><Envelope></Envelope></InputGroup.Text>
                                <Form.Control type="email" placeholder="Enter email" value={email} onChange={(e) => setEmail(e.target.value)} />
                            </InputGroup>
                        </Form.Group>
                    </Row>

                    <Form.Group className="mb-3" controlId="formGridContact">
                        <Form.Label visuallyHidden>Contact Number</Form.Label>
                        <InputGroup className="mb-2">
                            <InputGroup.Text className="bg-light"><Telephone></Telephone></InputGroup.Text>
                            <Form.Control type="number" placeholder="Enter Contact Number" value={mobile} onChange={(e) => setMobile(e.target.value)} />
                        </InputGroup>
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="formGridOrg">
                        <Form.Label visuallyHidden>Organisation</Form.Label>
                        <InputGroup className="mb-2">
                            <InputGroup.Text className="bg-light"><Building></Building></InputGroup.Text>
                            <Form.Control type="text" placeholder="Enter Organisation" value={org} onChange={(e) => setOrg(e.target.value)} />
                        </InputGroup>
                    </Form.Group>
                    <br></br>
                    <Row className=''>
                        <Col className="text-end">
                            <Button variant="primary" type="submit"  >
                                Update
                            </Button>
                        </Col>
                        <Col className="text-start">
                            <Button variant="danger" className="ms-2"   onClick={onDeleteMember} >
                                Delete
                            </Button>
                        </Col>
                    </Row>
                </Form>
            </Modal.Body>
        </Modal>
    );
}