import { useState } from "react";
import { Toast } from "react-bootstrap";
import {
  setToaster,
} from './../hisaabSlice';

import { useDispatch, useSelector } from 'react-redux';

export default function Toaster(props) {
  const dispatch = useDispatch();
  const toaster = useSelector(state => state.hisaab.toaster);
  return (
    <Toast bg='success' onClose={() => dispatch(setToaster({show:false}))} show={toaster?.show} delay={1500}
      style={{ zIndex: '10000', position: "fixed", top: 10, left: 5 }} autohide>
    {toaster?.header && <Toast.Header closeButton={false}>
        <strong className="me-auto">{toaster?.header}</strong>
      </Toast.Header>}
      <Toast.Body className="text-white">{toaster?.text}</Toast.Body>
    </Toast>
  );
}