import { ListGroup } from "react-bootstrap"
import { GeoAltFill } from "react-bootstrap-icons";
import { useSelector } from 'react-redux';
import styles from './../hisaab.module.css';
import { getColor } from './../subcomponents/colors';

export default function Transactions(props) {
    const groupId = props.groupId || 1;
    const members = useSelector((state) => state.hisaab.members.filter(m => m.groupId == groupId));
    const transactions = useSelector((state) => state.hisaab.transactions.filter(m => m.groupId == groupId).reverse());
    return (
        <div>
            <ListGroup>
                {transactions.map(t =>
                    <ListGroup.Item key={t.id}>
                        <div className={styles.transactionItems} >
                            <span>{members.find(m => m.id == t.spentBy)?.name} paid for {t.type}<br></br><span><GeoAltFill size={10}></GeoAltFill> {t.desc}</span></span><span className={styles.amount}> ₹{t.amount}</span>
                            <span className={styles.date}>{new Date(t.datetime).toDateString()}</span>
                        </div>
                    </ListGroup.Item>)}
            </ListGroup>
        </div>
    )
}